import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CompanylistComponent } from './companylist.component';
import { AddeditcompayComponent } from './addeditcompay.component';


const routes: Routes = [
  { path: '', component: CompanylistComponent, data: { title: 'List Role' } },
  { path: 'addcompany', component: AddeditcompayComponent},
  { path: 'editcompany/:id', component: AddeditcompayComponent}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ManagecompanyRoutingModule { }
